.przeglad-elektryczny {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 80px 0;
    gap: 40px;
    background-color: #ffffff;
    font-family: "Lato", sans-serif;
  
    .container {
      width: 100%;
      max-width: 1440px;
      margin: 0 auto;
      padding: 0 40px;
      display: flex;
      justify-content: space-between;
      gap: 40px;
  
      .left {
        flex: 1;
        max-width: 600px;
  
        h1 {
          font-size: 48px;
          font-weight: 300;
          color: #333;
          margin-bottom: 20px;
        }
  
        .description {
          font-size: 18px;
          line-height: 1.6;
          color: #555;
          margin-bottom: 30px;
        }
  
        .cta {
          background-color: #506446;
          color: white;
          border: none;
          border-radius: 30px;
          padding: 14px 26px;
          font-size: 16px;
          font-weight: 500;
          cursor: pointer;
          display: inline-flex;
          align-items: center;
          gap: 10px;
          transition: background-color 0.3s ease;
  
          &:hover {
            background-color: #3f5237;
          }
        }
  
        .steps {
          margin-top: 40px;
          font-size: 16px;
          color: #666;
  
          p {
            font-weight: 400;
            margin-bottom: 10px;
          }
  
          ol {
            padding-left: 20px;
  
            li {
              margin-bottom: 12px;
              line-height: 1.5;
            }
          }
        }
      }
  
      .right {
        flex: 1;
        max-width: 400px;
  
        .placeholder {
          width: 100%;
          height: 400px;
          background-color: #d3d3d3;
          border-radius: 10px;
        }
      }
    }
  }
  
  @media (max-width: 960px) {
    .przeglad-budowlany {
      flex-direction: column;
      align-items: center;
      padding: 40px 20px;
  
      .right {
        max-width: 100%;
        margin-top: 30px;
  
        .placeholder {
          height: 250px;
        }
      }
    }
  }
  